.text-center {
  text-align: center;
  /* margin: 20px; */
  color: black;
}

.latestheadline {
  margin: 0 14px;
  padding: 14px;
}

.cards {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.error {
  color: red;
  text-align: center;
}

.container {
  padding: 1rem;
}

.single-blog {
  box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.2);
  padding: 10px;
  margin-top: 30px;
  background-color: #fff;
}
.single-blog img {
  width: 100%;
  height: 15rem;
}
.blog-meta {
  font-size: 14px;
  margin-bottom: 2px;
  overflow: hidden;
}
.single-blog span {
  float: right;
  font-size: 12px;
  color: cornflowerblue;
}
.blog-text {
  font-size: 14px;
  text-align: justify;
}
.single-blog h2 {
  margin-top: 10px;
  font-size: 16px;
  color: #007bff;
}
.single-blog h2 a {
  text-decoration: none;
}

.read-more-btn {
  background-image: linear-gradient(to right, #1c03b5, #5c8bef);
  padding: 5px 12px 8px;
  border-radius: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #fff;
  border: none;
  text-decoration: none;
}
.read-more-btn:hover {
  background-image: linear-gradient(to right, #006417, #00b128);
  text-decoration: none;
  color: #fff;
}
