.vaccine-container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin: 10px;
  background-color: white;
}
.wrapper{
  font-weight: 400;
  line-height: 1.5;
  color: #4a4a4a;
  max-width: 100%;
  
}
.article{
  border: 1px solid white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  box-shadow: 0 0.5em 1em -0.125em rgba(10,10,10,.1), 0 0 0 1px rgba(10,10,10,.02);
  border-radius: .25rem;
}
.article h3{
  font-size: 1.3rem;
  margin-bottom: 8px;
  font-weight: 400;
}

.card-footer{
  font-size: 1em;
  border-top: 1px solid #ededed;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-footer-item{
  padding: 4px;
  color: #7957d5;
  cursor: pointer;
  text-decoration: none;
  
}

.article .institute{
  font-weight: 400;
  line-height: 1.5;
  color: #4a4a4a;
  font-size: .8rem;
  
}

.phase{
font-size: 1.25rem;
text-transform: uppercase;
}
.phase span{
text-transform: capitalize;
}

.vaccine-head{
display: flex;
justify-content: space-around;
font-size: .9rem;
font-weight: 200;
margin: 0 20px;
flex-wrap: wrap;
color: #7957d5;
}


@media screen and (max-width:800px){
  .vaccine-container{
    grid-template-columns: repeat(2, 1fr);
    margin: 7px;
  }
}
@media screen and (max-width:500px){
  .vaccine-container{
    grid-template-columns: 1fr;
    margin: 10px;
  }
  .vaccine-head{
    justify-content: space-between;
    }
}
